<template>
  <el-row type="flex" class="row-bg">
    <draggable
      tag="el-col"
      v-for="(t,i) in columns"
      :key="i"
      :span="t.span"
      :options="{group: { name: 'abc' }}"
      :list="t.list"
      @choose="(e)=>{choose(e,t)}"
      class="grid-content bg-purple"
    >
      <template v-for="(w,d) in t.list">
        <component
          :key="d"
          :is="w.type"
          :options="w.options"
          v-if="w.type!='hb-grid'"
          :data-key="w.key"
        ></component>
        <component :key="d" :is="w.type" :options="w" v-else :data-key="w.key"></component>
      </template>
    </draggable>
  </el-row>
</template>
<script>
import draggable from "vuedraggable";

export default {
  name: "hb-grid",
  props: ["options"],
  components: {
    draggable
  },
  data() {
    return {};
  },
  computed: {
    columns: function() {
      let colnums =
        this.options.options.colnums.value != ""
          ? this.options.options.colnums.value
          : 2;
      // this.options.columns = [];
      // for (let i = 0; i < Number(colnums); i++) {
      //   let obj = { span: 24 / colnums, list: [] };
      //   this.options.columns.push(obj);
      // }

      let arr = []
      for (let i = 0; i < Number(colnums); i++) {
        let obj = { span: 24 / colnums, list: [] };
        arr.push(obj);
      }
      // this.options.columns = arr
      // emit...
      return arr;
    }
  },
  methods: {
    choose(e) {
      this.$emit("chooseElement", e);
    }
  }
};
</script>
<style  scoped lang='less'>
.el-row {
  // margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
  border: 1px solid lightslategray;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 5px;
  min-height: 50px;
  padding: 10px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>