<template>
  <draggable
    :options="{group: { name: 'abc' }}"
    :list="comData"
    class="form"
    @choose="choose"
    @add="add"
  >
    <template v-for="(item,i) in comData ">
      <el-form-item
        :label="item.data.itemName?item.data.itemName:'组件标题'"
        :key="item.key + i"
        :data-key="item.key"
        :required="item.data.required?item.data.required:false"
      >
        <component :is="item.componentName" v-bind="item.data" :comData="item.data"></component>
      </el-form-item>
    </template>
  </draggable>
</template>

<script>
import draggable from "vuedraggable";
export default {
  name: "hbFormItem",
  props: {
    comData: {
      type: Array
    },
    childrenOptions: {
      type: Object
    }
  },
  components: {
    draggable
  },
  methods: {
    choose(e) {
      this.$emit("chooseElement", e);
    },
    add(e) {
      let obj = this.comData[e.newIndex];
      this.$emit("changeItem", obj, this.childrenOptions);
      this.$forceUpdate();
    }
  }
};
</script>

<style scoped>
.form {
  width: 96%;
  min-height: 100px;
  border: 1px solid black;
  padding: 20px;
}
</style>