<template>
  <div id="app">
    <!-- 头部 -->
    <div style="float:right;">
      <el-button type="success" round @click="downLoadJson">导出页面</el-button>
      <el-button type="danger" round @click="()=>{this.deleteComponent(this.showList)}">删除组件</el-button>
    </div>
    <div class="box">
      <!-- 左侧拖拽菜单 -->
      <draggable
        :list="startList"
        class="leftBox"
        :options="options"
        @clone="clone"
        @remove="remove"
      >
        <p v-for="(item,index) in startList" :key="index" class="p">{{item.name}}</p>
      </draggable>
      <!-- 主舞台 -->
      <draggable
        :list="showList"
        class="centerBox"
        :options="{group: { name: 'abc' }}"
        @choose="(e)=>{choose(e,this.showList)}"
      >
        <template v-for="(t,i) in showList">
          <!-- form 容器 -->
          <component
            :key="i"
            :is="t.componentName"
            v-bind="t.data"
            v-if="t.componentName=='hb-form'"
            :data-key="t.key"
          >
            <!--  子容器 -->
            <hbFormItem
              :comData="t.children"
              :childrenOptions="t.childrenOptions"
              :rules="t.rules"
              @chooseElement="chooseElement"
              @changeItem="changeItem"
            />
          </component>
          <!-- 非form容器 -->
          <component
            :key="i"
            :is="t.componentName"
            :comData="t.data"
            v-bind="t.data"
            v-else
            :data-key="t.key"
            ref="ceshi"
          ></component>
        </template>
      </draggable>
      <!-- 右侧属性栏 -->
      <div class="rightBox">
        <el-tabs v-model="activeName">
          <el-tab-pane label="可配置属性" name="first">
            <showAtr :currentData="currentData" @setCurrentData="setCurrentData" />
          </el-tab-pane>
          <el-tab-pane label="数据结构" name="second">数据结构</el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import appMix from "./app.js";
import showAtr from "./basicsComponents/showAtr";
import hbGrid from "./components/grid/hb_grid.vue";
import hbFormItem from "./components/form/hb_form_item";
import hbirdOptions from "@hbird/comps/dist/hbirdOptions.json";
export default {
  components: {
    draggable,
    showAtr,
    hbGrid,
    hbFormItem
  },
  mixins: [appMix],
  mounted() {
    this.startList = hbirdOptions;
  },
  data() {
    return {
      activeName: "first",
      options: {
        sort: false,
        dragClass: "pDrag",
        chosenClass: "chosenClass",
        group: { name: "abc", pull: "clone", put: false }
      },
      showList: [], //舞台中显示的数据
      startList: [], //初始化数据
      currentData: {} //选中数据
    };
  },
  methods: {
    chooseElement(e) {
      this.choose(e, this.showList);
    },
    choose(e, arr) {
      arr &&
        arr.forEach(t => {
          if (e.item.dataset.key == t.key) {
            this.currentData = t;
          } else {
            this.choose(e, t.children);
          }
        });
    },
    remove(e) {
      this.startList[e.oldIndex] = JSON.parse(
        JSON.stringify(this.startList[e.oldIndex])
      );
      this.showList.forEach(t => {
        //判断 如果已经存在 form表单  不允许再次往里面添加form 表单
        t.children &&
          t.children.forEach((w, i) => {
            if (w.name == "表单容器" || w.name == "表格") {
              t.children.splice(i, 1);
              this.$message.error("表单组件里不能嵌套表单或者表格");
            }
          });
      });
    },
    clone(e) {
      this.startList = [];
      this.startList = hbirdOptions;
      let _obj = JSON.parse(JSON.stringify(this.startList[e.oldIndex]));
      //克隆时加上唯一id给组件
      _obj.key = new Date().getTime();
      this.startList[e.oldIndex] = _obj;
    }
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.box {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50px;
  left: 0;
}

.btnGroup {
  position: absolute;
  right: 0;
}

.p {
  width: 100%;
  padding: 20px 20px 20px 2px;
  text-align: center;
  background: white;
  border: 1px solid black;
}
.pDrag {
  width: 100%;
  padding: 20px 20px 20px 2px;
  text-align: center;
  background: white;
  border: 3px dashed lightskyblue;
}
.chosenClass {
  width: 100%;
  padding: 20px 20px 20px 2px;
  text-align: center;
  background: lightcyan;
  box-shadow: 0 0 10px rgb(6, 155, 241);
  opacity: 0.5;
}
.box {
  display: flex;
  width: 100%;
}
.leftBox {
  width: 10%;
  min-height: 100%;
  background: gray;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-left: 1px solid black;
}
.centerBox {
  width: 76%;
  min-height: 100%;
  background: white;
  border: 1px solid black;
  left: "15%";
  position: "absolute";
}
.rightBox {
  width: 15%;
  min-height: 100%;
  background: white;
  position: absolute;
  right: -2px;
  border: 1px solid black;
}
.rightBox .el-tree {
  color: rebeccapurple;
}
.rightBox .el-tree-node__expand-icon {
  color: black;
}
.rightBox >>> .el-tabs__item {
  padding-left: 45px !important;
}
</style>
